@import url(https://fonts.googleapis.com/css?family=Raleway:400,700,800);
*,
*:after,
*:before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

/*body {*/
/*height: 100%;*/
/*background: #373a47;*/
/*color: #fffce1;*/
/*font-family: 'Raleway', Arial, sans-serif;*/
/*}*/

#app {
    height: 100%;
}

a {
    text-decoration: none;
}

a:hover,
a:focus {
    color: #c94e50;
}

main {
    height: 100%;
    overflow: auto;
}

.description {
    max-width: 20em;
    margin: 1.2em auto 1em;
    line-height: 1.3em;
    font-weight: 400;
}

.demo-buttons {
    font-size: 1em;
    max-width: 1200px;
    margin: 2em auto 3em;
    padding: 0 10em;
}

.demo-buttons a {
    display: inline-block;
    margin: 0.75em;
    padding: 1.35em 1.1em;
    width: 15em;
    background: #fffce1;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 800;
    border-top-left-radius: 20px 50px;
    border-top-right-radius: 20px 50px;
    border-bottom-right-radius: 20px 50px;
    border-bottom-left-radius: 20px 50px;
    cursor: pointer;
}

.demo-buttons a.current-demo {
    background: #c94e50;
    color: #fffce1;
}

.side-button {
    display: inline-block;
    width: 5em;
    height: 2.5em;
    line-height: 2.5em;
    cursor: pointer;
    background: #fffce1;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.8em;
    font-weight: 800;
}

.side-button.left {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.side-button.right {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.side-button.active {
    background: #c94e50;
    color: #fffce1;
}

.Image {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3), 0 6px 20px 0 rgba(0, 0, 0, 0.5);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3), 0 6px 20px 0 rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 40em) {

    .Image {
        max-width: 100%;
    }

    main {
        font-size: 80%;
    }

    h1 {
        padding-top: .2vh;
        font-size: 2.5em;
    }

    .demo-buttons {
        max-width: 900px;
        padding: 0 2em;
    }

    .img {
        width: 10px;
    }
}

.WideImage {
    height: auto;
    width: 80vh;
    max-width: 95%;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.bm-burger-button {
    position: absolute;
    width: 36px;
    height: 30px;
    left: 36px;
    top: 10px;

}

.right .bm-burger-button {
    left: initial;
    right: 36px;

}

.bm-burger-bars {
    background: white;
}

.bm-morph-shape {
    fill: #373a47;
}

.bm-menu {
    /*background: #373a47;*/
    background: #263238;
}

.bm-menu a {
    color: #b8b7ad;
}

.bm-menu a:hover,
.bm-menu a:focus {
    color: #c94e50;
}

.bm-item-list a {
    padding: 0.8em;
}

.bm-item-list a span {
    margin-left: 10px;
    font-weight: 700;
}

.menu-1 .bm-cross {
    background: #bdc3c7;
}

.menu-1 .bm-menu {
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
}

.menu-2 .bm-cross {
    background: #999;
}

.menu-2 .bm-menu {
    padding: 3.4em 1em 0;
}

.menu-2 a {
    padding: 1em;
}

.menu-2 i {
    font-size: 1.7em;
    vertical-align: middle;
    color: #282a35;
}

.menu-3 .bm-cross {
    background: #888;
}

.menu-3 .bm-menu {
    padding: 2em 1em;
    font-size: 1.15em;
}

.menu-3 i {
    opacity: 0.5;
}

.menu-3 span {
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.75em;
}

.menu-4 .bm-cross {
    background: #888;
}

.menu-4 h2 {
    margin: 0 auto;
    padding: 2em 1em;
    color: rgba(0, 0, 0, 0.4);
    -webkit-box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2);
            box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2);
}

.menu-4 h2 i {
    margin-left: 0.2em;
}

.menu-4 h2 span {
    font-size: 1.6em;
    font-weight: 700;
}

.menu-4 a {
    padding: 1em;
    text-transform: uppercase;
    -webkit-transition: background 0.3s, -webkit-box-shadow 0.3s;
    transition: background 0.3s, -webkit-box-shadow 0.3s;
    -o-transition: background 0.3s, box-shadow 0.3s;
    transition: background 0.3s, box-shadow 0.3s;
    transition: background 0.3s, box-shadow 0.3s, -webkit-box-shadow 0.3s;
    -webkit-box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2);
            box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2);
}

.menu-4 a span {
    letter-spacing: 1px;
    font-weight: 400;
}

.menu-4 a:hover,
.menu-4 a:focus {
    background: rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: inset 0 -1px rgba(0, 0, 0, 0);
            box-shadow: inset 0 -1px rgba(0, 0, 0, 0);
    color: #b8b7ad;
}

#slide .bm-cross,
#stack .bm-cross,
#reveal .bm-cross {
    background: #bdc3c7;
}

#slide .bm-menu,
#stack .bm-menu,
#reveal .bm-menu {
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
}

#bubble .bm-cross,
#push .bm-cross {
    background: #999;
}

#bubble .bm-menu,
#push .bm-menu {
    padding: 3.4em 1em 0;
}

#bubble a,
#push a {
    padding: 1em;
}

#bubble i,
#push i {
    font-size: 1.7em;
    vertical-align: middle;
    color: #282a35;
}

#elastic .bm-cross {
    background: #888;
}

#elastic .bm-menu {
    padding: 2em 1em;
    font-size: 1.15em;
}

#elastic i {
    opacity: 0.5;
}

#elastic span {
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.75em;
}

#scaleDown .bm-cross,
#scaleRotate .bm-cross,
#pushRotate .bm-cross,
#fallDown .bm-cross {
    background: #888;
}

#scaleDown h2,
#scaleRotate h2,
#pushRotate h2,
#fallDown h2 {
    margin: 0 auto;
    padding: 2em 1em;
    color: rgba(0, 0, 0, 0.4);
    -webkit-box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2);
            box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2);
}

#scaleDown h2 i,
#scaleRotate h2 i,
#pushRotate h2 i,
#fallDown h2 i {
    margin-left: 0.2em;
}

#scaleDown h2 span,
#scaleRotate h2 span,
#pushRotate h2 span,
#fallDown h2 span {
    font-size: 1.6em;
    font-weight: 700;
    color: white;
}

#scaleDown a,
#scaleRotate a,
#pushRotate a,
#fallDown a {
    padding: 1em;
    text-transform: uppercase;
    -webkit-transition: background 0.3s, -webkit-box-shadow 0.3s;
    transition: background 0.3s, -webkit-box-shadow 0.3s;
    -o-transition: background 0.3s, box-shadow 0.3s;
    transition: background 0.3s, box-shadow 0.3s;
    transition: background 0.3s, box-shadow 0.3s, -webkit-box-shadow 0.3s;
    -webkit-box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2);
            box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2);
}

#scaleDown a span,
#scaleRotate a span,
#pushRotate a span,
#fallDown a span {
    letter-spacing: 1px;
    font-weight: 400;
}

#scaleDown a:hover,
#scaleRotate a:hover,
#pushRotate a:hover,
#fallDown a:hover,
#scaleDown a:focus,
#scaleRotate a:focus,
#pushRotate a:focus,
#fallDown a:focus {
    background: rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: inset 0 -1px rgba(0, 0, 0, 0);
            box-shadow: inset 0 -1px rgba(0, 0, 0, 0);
    color: #bdc3c7;
}

.MenuContainer {

    padding-top: 25px;
    padding-bottom: 25px;
    cursor: pointer;
}

.MenuItem {
    color: #ADADAD;
    letter-spacing: 1px;
    font-weight: 400;
    font-size: 1.2em;
    padding-left: 25px;
    text-align: center;
}

.WhiteText {
    color: white;
}


